.featured{
    width: 100%;
    display: flex;
    justify-content: space-between;
}

.featured-item{
    flex: 1;
    margin: 0 20px;
    padding: 30px;
    border-radius: 10px;
    cursor: pointer; 
    transition: box-shadow ease-in .2s;
    box-shadow: 0 0 15px -10px rgba(0, 0 , 0, .75);

    &:hover{
        box-shadow: 0 0 15px -7px rgba(0, 0 , 0, .75);
    }

    .featured-title{
        font-size: 20px;
    }

    .featured-money-container{
        margin: 10px 0;
        display: flex;
        align-items: center;

        .featured-money{
            font-size: 30px;
            font-weight: 600;
        }

        .featured-money-rate{
            display: flex;
            align-items: center;
            margin-left: 20px;
        }
    }

    .featured-sub{
        color: grey;
        font-size: 15px;
    }
}

.featured-icon{
    font-size: 14px;
    color: green;
    margin-left: 5px;

    &.negative{
        color: red;
    }

}